.HomePage {
  display: flex;
  justify-content: center;
  position: relative;
  top: 4em;
  background-color: var(--darkBlue);
}

.HomeBackground {
  width: 100%;
}

.HomeTitleIntro {
  margin: 0;
  margin-top: calc(0.5em + 1vw);
  font-weight: 400;
  font-size: var(--webSmallFont5);
  color: var(--shade2White);
  line-height: 1.3em;
}

.HomeTextWeb {
  position: absolute;
  color: var(--shade2White);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 40%;
  top: calc(4em + 5vw);
  left: 7%;
}

.HomeTextMob {
  display: none;
}

.HomeTitle {
  margin: 0.3em 0 0;
  font-weight: 700;
  font-style: normal;
  font-size: calc(0.1em + 3.5vw);
  color: var(--baseWhite);
}

.btnhomeR {
  border-radius: 8px;
  color: var(--baseWhite);
  padding: 0.3em 1.1em;
  cursor: pointer;
  background-color: var(--baseOrange);
  font-size: calc(0.3em + 1.1vw);
  border: 1px solid var(--baseOrange);
  display: flex;
  align-items: center;
  margin-top: calc(0.3em + 3vw);
  transition: all 0.3s;
  margin-right: 20px;
}

.btnhomeR:hover {
  border: 1px solid var(--baseWhite);
  background-color: transparent;
}

.hidden {
  opacity: 0;
}

.PlaneInButtonR {
  height: calc(1em + 0.9vw);
  width: calc(1em + 1.5vw);
  padding-right: 0.5em;
}

.homeButBox {
  display: flex;
}

.HomePageMetaData {
  display: flex;
  justify-content: space-around;
  margin-top: calc(3em + 1vw);
  padding-top: 2em;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.HomePageMetaData h3 {
  font-size: var(--webNormalFont);
  color: var(--baseWhite);
  text-align: center;
}

.HomeDreamer {
  position: absolute;
  right: 7%;
  top: calc(1em + 5vw);
  display: flex;
  justify-content: flex-end;
}

